.wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 194px;
    background: #F6F7F8;
    border: 1px solid #C1D9E6;
    border-radius: 5px;
    overflow-y: scroll;
}

.close {
    position: absolute;
    right: 16px;
    top: 17px;
    width: 16px;
    height: 16px;
    background-image: url('./svg/close.svg');
}